import React, { useState, useEffect } from 'react'
import { Label, Input, FormGroup, Button } from 'reactstrap'
import { X } from 'react-feather'
import PerfectScrollbar from 'react-perfect-scrollbar'
import classnames from 'classnames'
import themeConfig from '../../configs/themeConfig'

const DataListSidebar = (props) => {
  const [relation, setrelation] = useState('Collegue')
  const [relation1, setrelation1] = useState('Collegue')
  const [email, setemail] = useState('')
  const [address, setaddress] = useState('')
  const [contact1, setcontact1] = useState('')
  const [contact2, setcontact2] = useState('')
  const [img, setimg] = useState('')
  const [id, setid] = useState('')
  const [name, setname] = useState('')
  const [addNew, setaddNew] = useState(false)
  const [errorrelation1, seterrorrelation1] = useState('')
  const [erroremail, seterroremail] = useState('')
  const [erroraddress, seterroraddress] = useState('')
  const [errorcontact1, seterrorcontact1] = useState('')
  const [errorcontact2, seterrorcontact2] = useState('')
  const [errorname, seterrorname] = useState('')

  useEffect(() => {
    if (props.data) {
      const {
        id,
        name,
        relation,
        address,
        email,
        primaryContact,
        secondaryContact
      } = props.data
      setid(id)
      setname(name)
      setrelation(relation)
      if (
        relation !== 'Collegue' &&
        relation !== 'Cousin' &&
        relation !== 'Daughter' &&
        relation !== 'Daughter-in-law' &&
        relation !== 'Friend' &&
        relation !== 'Husband' &&
        relation !== 'Son' &&
        relation !== 'Son-in-law' &&
        relation !== 'Wife'
      ) {
        setrelation('others')
        setrelation1(relation)
      }
      setaddress(address)
      setemail(email)
      setcontact1(primaryContact)
      setcontact2(secondaryContact)
    } else if (props.data === null || addNew) {
      nullData()
    }
    setaddNew(false)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.data])

  const handleValidation = (type) => {
    let valid = true
    switch (type) {
      case 'name':
        if (name.length === 0 || name === '') {
          seterrorname('Enter Valid Name')
          valid = false
        } else {
          seterrorname('')
          valid = true
        }
        break
      case 'email':
        if (email === '') {
          seterroremail('Enter Valid Email')
          valid = false
        } else if (email !== '') {
          if (typeof email !== 'undefined') {
            if (
              !email.match(
                /^([A-Za-z0-9_\-.])+@([A-Za-z0-9_\-.])+\.([A-Za-z]{2,})$/
              )
            ) {
              seterroremail('Enter Valid Email')
              valid = false
            } else {
              seterroremail('')
              valid = true
            }
          }
        }
        break
      case 'address':
        if (!address) {
          seterroraddress('Enter Valid Address')
          valid = false
        } else {
          seterroraddress('')
          valid = true
        }
        break
      case 'contact1':
        if (contact1.length === 0) {
          seterrorcontact1('Enter Valid Contact')
          valid = false
        } else if (contact1.length !== 0) {
          if (typeof contact1 !== 'undefined') {
            if (contact1.length !== 10) {
              seterrorcontact1('Enter Valid Contact')
              valid = false
            } else {
              seterrorcontact1('')
              valid = true
            }
          }
        }
        break
      case 'contact2':
        if (contact2.length === 0) {
          seterrorcontact2('')
          valid = true
        } else if (contact2.length !== 0 && contact2.length === 10) {
          seterrorcontact2('')
          valid = true
        } else {
          seterrorcontact2('Enter Valid Contact')
          valid = false
        }
        break
      case 'relation':
        if (relation === 'others' && relation1.length === 0) {
          seterrorrelation1('Enter Valid Relation')
          valid = false
        } else {
          seterrorrelation1('')
          valid = true
        }
        break
      case 'submit':
        if (name.length === 0 || name === '') {
          seterrorname('Enter Valid Name')
          valid = false
        } else {
          seterrorname('')
          valid = true
        }
        if (email === '') {
          seterroremail('Enter Valid Email')
          valid = false
        } else if (email !== '') {
          if (typeof email !== 'undefined') {
            if (
              !email.match(
                /^([A-Za-z0-9_\-.])+@([A-Za-z0-9_\-.])+\.([A-Za-z]{2,})$/
              )
            ) {
              seterroremail('Enter Valid Email')
              valid = false
            } else {
              seterroremail('')
              valid = true
            }
          }
        }
        if (
          relation === 'others' &&
          (relation1.length === 0 || relation1 === 'others')
        ) {
          seterrorrelation1('Enter Valid relation')
        } else {
          seterrorrelation1('')
        }
        if (contact1.length === 0) {
          seterrorcontact1('Enter Valid Contact')
          valid = false
        } else if (contact1.length !== 0) {
          if (typeof contact1 !== 'undefined') {
            if (contact1.length !== 10) {
              seterrorcontact1('Enter Valid Contact')
              valid = false
            } else {
              seterrorcontact1('')
              valid = true
            }
          }
        }

        if (contact2.length === 0) {
          seterrorcontact2('')
          valid = true
        } else if (contact2.length !== 0 && contact2.length === 10) {
          seterrorcontact2('')
          valid = true
        } else {
          seterrorcontact2('Enter Valid Contact')
          valid = false
        }

        if (!address) {
          seterroraddress('Enter Valid Address')
          valid = false
        } else {
          seterroraddress('')
          valid = true
        }
        break
      default:
        break
    }
    return valid
  }

  const handleValue = (type, value) => {
    switch (type) {
      case 'name':
        setname(value)

        break
      case 'email':
        setemail(value)

        break
      case 'address':
        setaddress(value.replace(/[\n\r]/g, ', '))

        break
      case 'contact1':
        setcontact1(value)
        break
      case 'contact2':
        setcontact2(value)

        break
      case 'relation':
        setrelation(value)

        break
      case 'relation1':
        setrelation1(value)

        break
      default:
        break
    }
  }

  const resetError = (type) => {
    switch (type) {
      case 'name':
        seterrorname('')
        break
      case 'email':
        seterroremail('')
        break
      case 'address':
        seterroraddress('')
        break
      case 'contact1':
        seterrorcontact1('')
        break
      case 'contact2':
        seterrorcontact2('')
        break
      case 'relation1':
        seterrorrelation1('')
        break
      default:
        break
    }
  }

  const handleSubmit = () => {
    const valid = handleValidation('submit')
    if (valid) {
      const obj = {
        relation,
        relation1,
        email,
        address,
        contact1,
        contact2,
        id,
        name
      }
      console.log('here', obj)
      if (props.data !== null) {
        obj._id = props.data._id
        obj.usage = props.data.usage
        props.updateData(obj)
      } else {
        setaddNew(true)
        props.addData(obj)
      }
    }
  }

  const nullData = () => {
    setname('')
    setrelation('Collegue')
    setrelation1('')
    setemail('')
    setaddress('')
    setcontact1('')
    setcontact2('')
    seterrorname('')
    seterroraddress('')
    seterrorcontact1('')
    seterrorcontact2('')
    seterroremail('')
    seterrorrelation1('')
  }

  let { show, handleSidebar, data } = props
  return (
    <div
      className={classnames('data-list-sidebar', {
        show: show
      })}
    >
      <div className="data-list-sidebar-header mt-2 px-2 d-flex justify-content-between">
        <h4 style={{ color: 'var(--warning)' }}>
          {data !== null ? 'UPDATE NOMINEE' : 'ADD NEW NOMINEE'}
        </h4>
        <X
          size={20}
          onClick={() => {
            handleSidebar(false, true)
            nullData()
          }}
        />
      </div>
      <PerfectScrollbar
        className="data-list-fields px-2 pt-2"
        options={{ wheelPropagation: false }}
      >
        <div>
          <FormGroup className="form-label-group mb-0">
            <Input
              className="input-label"
              type="text"
              value={name}
              placeholder="Nominee Name"
              onChange={(e) => {
                handleValue('name', e.target.value)
                resetError('name')
              }}
              onBlur={() => handleValidation('name')}
              id="data-name"
              style={{ borderColor: errorname ? 'red' : '' }}
            />
            <Label
              className={
                themeConfig.theme === 'dark' ? 'dark-label' : 'light-label'
              }
              for="data-name"
            >
              Name
            </Label>
          </FormGroup>
          {errorname && (
            <span style={{ color: 'red', fontSize: '0.8rem' }}>
              {errorname}
            </span>
          )}
        </div>
        <FormGroup className="mt-2 mb-0">
          <Input
            type="select"
            id="data-category"
            value={relation}
            onChange={(e) => {
              handleValue('relation', e.target.value)
            }}
          >
            <option>Collegue</option>
            <option>Cousin</option>
            <option>Daughter</option>
            <option>Daughter-in-law</option>
            <option>Friend</option>
            <option>Husband</option>
            <option>Son</option>
            <option>Son-in-law</option>
            <option>Wife</option>
            <option>others</option>
          </Input>
          <Label
            className={
              themeConfig.theme === 'dark'
                ? 'dark-label select-label'
                : 'light-label select-label'
            }
            for="data-category"
          >
            Relation
          </Label>
        </FormGroup>
        {relation === 'others' && (
          <FormGroup className="form-label-group mt-2 mb-0">
            <Input
              type="text"
              value={relation1}
              placeholder="Relation Name"
              onChange={(e) => {
                handleValue('relation1', e.target.value)
                resetError('relation1')
              }}
              onBlur={() => handleValidation('relation1')}
              id="data-Relation"
              style={{ borderColor: errorrelation1 ? 'red' : '' }}
            />
          </FormGroup>
        )}
        {errorrelation1 && (
          <span style={{ color: 'red', fontSize: '0.8rem' }}>
            {errorrelation1}
          </span>
        )}
        <FormGroup className="form-label-group mt-2 mb-0">
          <Input
            className="input-label"
            type="email"
            value={email}
            placeholder="Enter nominee email"
            onChange={(e) => {
              handleValue('email', e.target.value)
              resetError('email')
            }}
            onBlur={() => handleValidation('email')}
            id="data-email"
            style={{ borderColor: erroremail ? 'red' : '' }}
          />
          <Label
            className={
              themeConfig.theme === 'dark' ? 'dark-label' : 'light-label'
            }
            for="data-email"
          >
            Nominee Email
          </Label>
        </FormGroup>
        {erroremail && (
          <span style={{ color: 'red', fontSize: '0.8rem' }}>{erroremail}</span>
        )}
        <FormGroup className="form-label-group mt-2 mb-0">
          <Input
            className="input-label"
            type="number"
            value={contact1}
            onChange={(e) => {
              handleValue('contact1', e.target.value)
              resetError('contact1')
            }}
            onBlur={() => handleValidation('contact1')}
            id="data-price"
            placeholder="Enter primary contact No"
            style={{ borderColor: errorcontact1 ? 'red' : '' }}
          />
          <Label
            className={
              themeConfig.theme === 'dark' ? 'dark-label' : 'light-label'
            }
            for="data-price"
          >
            Primary Contact
          </Label>
        </FormGroup>
        {errorcontact1 && (
          <span style={{ color: 'red', fontSize: '0.8rem' }}>
            {errorcontact1}
          </span>
        )}
        <FormGroup className="form-label-group mt-2 mb-0">
          <Input
            className="input-label"
            type="number"
            value={contact2}
            onChange={(e) => {
              handleValue('contact2', e.target.value)
              resetError('contact2')
            }}
            onBlur={() => handleValidation('contact2')}
            id="data-price"
            placeholder="Enter secondry contact No"
            style={{ borderColor: errorcontact2 ? 'red' : '' }}
          />
          <Label
            className={
              themeConfig.theme === 'dark' ? 'dark-label' : 'light-label'
            }
            for="data-price"
          >
            Secondary Contact
          </Label>
        </FormGroup>
        {errorcontact2 && (
          <span style={{ color: 'red', fontSize: '0.8rem' }}>
            {errorcontact2}
          </span>
        )}
        <FormGroup className="form-label-group mt-2 mb-0">
          <Input
            className="input-label "
            style={{
              height: '115px',
              borderColor: erroraddress ? 'red' : ''
            }}
            type="textarea"
            value={address}
            placeholder="Address"
            onChange={(e) => {
              handleValue('address', e.target.value)
              resetError('address')
            }}
            onBlur={() => handleValidation('address')}
            id="data-Address"
          />
          <Label
            className={
              themeConfig.theme === 'dark' ? 'dark-label' : 'light-label'
            }
            for="data-Address"
          >
            Address *
          </Label>
        </FormGroup>
        {erroraddress && (
          <span style={{ color: 'red', fontSize: '0.8rem' }}>
            {erroraddress}
          </span>
        )}

        {props.thumbView && img.length <= 0 ? (
          <label
            className="btn btn-primary"
            htmlFor="upload-image"
            color="primary"
          >
            Upload Image
            <input
              type="file"
              id="upload-image"
              hidden
              onChange={(e) => setimg(URL.createObjectURL(e.target.files[0]))}
            />
          </label>
        ) : null}
      </PerfectScrollbar>
      <div className="data-list-sidebar-footer px-2 d-flex justify-content-start align-items-center mt-1">
        <Button.Ripple
          className="button-label"
          color="secondary"
          outline
          onClick={() => {
            nullData()
          }}
        >
          Reset
        </Button.Ripple>
        <Button.Ripple
          //disabled
          color="warning"
          type="reset"
          className="button-label"
          onClick={() => handleSubmit()}
        >
          {data !== null
            ? props.isLoading
              ? 'Updating...'
              : 'Update'
            : props.isLoading
            ? 'Adding...'
            : 'Add'}
        </Button.Ripple>
      </div>
    </div>
  )
}
export default DataListSidebar
